import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Autocomplete from 'react-autocomplete';
// import logo from './logo.svg';
// import './index.css';
// import './App.css';

// Components
import URLBlockList from './URLBlockList';
// import URLAlarmList from './URLAlarmList';

class Table extends Component {
	constructor(props) {
		super(props);
		// this.state = {
		// 	History: {
		// 	'google.com': [1, 2, [30,60], ["js", "styles"], ["day", "week"] ],
		// 	'bing.com': [1, 2, [45,15], ["images", "js"], ["week", "week"] ]
		// 	}
		// };
		this.state = {
			History: {}
		};

		//update state from chrome
		this.storageUpdate("simpleupdate", null);

		this.storageUpdate = this.storageUpdate.bind(this);
		this.handleNewURL = this.handleNewURL.bind(this);
		this.handleNewAlarm = this.handleNewAlarm.bind(this);
		this.handleEditAlarm = this.handleEditAlarm.bind(this);
	}

	storageUpdate(operation, givenURL) {
		var url = givenURL;


		chrome.storage.sync.get('History', function(data) {
			var History;

			// console.log("1");
			if (data['History'] == null) {
	  			History = {"*://*.google.com/*": [0, "", [0], [""], [""]]};
	  			console.log("Empty History: created new history");
	  		} else {
	  			History = data['History'];
	  		}


	  		//handlenewURL
	  		if (operation === "newurl") {

	  			// parse new url
	  			url = 'https://' + url + '/';
		  		var domain = url.match(/^[\w-]+:\/{2,}\[?([\w\.:-]+)\]?(?::[0-9]*)?/)[1];
		  		if (domain.startsWith("www.")) {
		  			domain = domain.substring(4);
		  		}

			 	// Prepare domain to match with History index
			 	domain = '*://*.'+domain+'/*';
			 	// domain = bgpg.wrapDomain(domain);

	  			History[domain] = [0, 0, [0], [""], [""]];
	  		}
	  		//handlenewalarm
	  		else if (operation === "newalarm") {
	  			History[url][2].push(1);
				History[url][3].push("js");
				History[url][4].push("day");
	  		}

	  		this.setState({
	  			History: History
	  		})

			chrome.storage.sync.set({'History': History});

		}.bind(this));
	}

	handleNewURL(newURL) {
	 	// pass raw newurl to storageUpdate
	 	this.storageUpdate("newurl", newURL);
	}

	handleNewAlarm(event) {
		event.preventDefault();
		var url = event.target.value;

		this.storageUpdate("newalarm", url);
	}

	handleEditAlarm(val) {
		var infobundle = val;
		var url = infobundle["url"];
		var id = infobundle["id"];

		chrome.storage.sync.get('History', function(data) {
			var History;

			if (data['History'] == null) {
	  			History = {"*://google.com/*": [0, "", [0], [""], [""]]};
	  			console.log("Empty History: created new history");
	  		} else {
	  			History = data['History'];
	  		}

	  		//Logic
			if (infobundle["operation"] === "save") {
				// convert min/hr to ms
				if (infobundle["durunit"] === "min") {
					History[url][2][id] = Number(infobundle["durmin"]) * 60000;
				}
				else if (infobundle["durunit"] === "hour") {
					History[url][2][id] = Number(infobundle["durmin"]) * 60000 * 60;
				}
				else {
					History[url][2][id] = Number(infobundle["durmin"]);
				}

				History[url][3][id] = infobundle["type"];
				History[url][4][id] = infobundle["per"];
			}
			else if (infobundle["operation"] === "delete") {
				History[url][2].splice(id, 1);
				History[url][3].splice(id, 1);
				History[url][4].splice(id, 1);
			}

	  		this.setState({
	  			History: History
	  		})

	  		chrome.storage.sync.set({'History': History});
		}.bind(this));	

	}

	render() {
		return (
			<div className="Table">
				<URLBlockList data={this.state.History} newalarmhandler={this.handleNewAlarm}
				editalarmhandler={this.handleEditAlarm}/>
				<AddURLButton data={this.state.History} onAddURL={this.handleNewURL}/>
			</div>
		);
	}

}

let styles = {
	item: {
		padding: '2px 6px',
		cursor: 'default'
	},
	highlightedItem: {
		color: 'white',
		background: 'hsl(200, 50%, 50%)',
		padding: '2px 6px',
		cursor: 'default'
	},
	menu: {
		border: 'solid 1px #ccc'
	}
}

// data, onAddURL
class AddURLButton extends Component {
	constructor(props) {
		super(props);
		this.state = {
			newurl: "",
			addURLInputField: false,
			errormsg: ""
		};

		this.onClickAddURL = this.onClickAddURL.bind(this);
		this.handleURLChange = this.handleURLChange.bind(this);
		this.handleURLButtonSX = this.handleURLButtonSX.bind(this);
		this.checkError = this.checkError.bind(this);

		this.getItems = this.getItems.bind(this);
		this.matchURL = this.matchURL.bind(this);
		this.sortStates = this.sortStates.bind(this);
	}

	onClickAddURL() {
		this.setState({
			addURLInputField: true
		});
	}

	handleURLChange(event) {
		this.setState({
			newurl: event.target.value
		})
	}

	handleURLButtonSX(event) {
		event.preventDefault();
		var operation = event.target.value;

		if (operation === "save") {
			if (this.state.newurl === "") {
				this.setState({
					errormsg: "nourl"
				});
				return;
			}
			this.setState({
				errormsg: ""
			});
			this.props.onAddURL(this.state.newurl);
		}

		// delete input field (X field on addnewurl)
		this.setState({
			newurl: "",
			addURLInputField: false
		});
	}

	checkError() {
		var errormsg = this.state.errormsg;
		if (errormsg !== "") {
			if (errormsg === "nourl" && this.state.addURLInputField) {
				return (
					<div className="errormsg">
						<p>Cannot add empty URL</p>
					</div>
				);
			}
		}
	}

	// <input 
	// type="text"
	// name="newurl"
	// value={this.state.newurl}
	// onChange={this.handleURLChange} />

	getItems() {
		var urls = Object.keys(this.props.data);
		// console.log(urls);
		return (urls);
	};

	matchURL(url, value) {
		return (
			url.indexOf(value) !== -1
		);
	};

	sortStates(a, b, value) {
		const aLower = a
		const bLower = b
		const valueLower = value
		const queryPosA = aLower.indexOf(valueLower)
		const queryPosB = bLower.indexOf(valueLower)
		if (queryPosA !== queryPosB) {
			return queryPosA - queryPosB
		}
		return aLower < bLower ? -1 : 1
	};

	// change autocomplete to only look at sites with no alarms in history.
	render() {
		var inputfield;
		if (this.state.addURLInputField) {
			inputfield = (
				<div className="addurlfield">
					
					<Autocomplete
					className="addurl-input"
					value={this.state.newurl}
					inputProps={{ name: 'A URL', id: 'urlAutoComplete' }}
					items={this.getItems()}
					getItemValue={(item) => item}
					shouldItemRender={this.matchURL}
					sortItems={this.sortStates}
					onChange={(event, value) => this.setState({ newurl: value })}
					onSelect={value => this.setState({ newurl: value })}
					renderItem={(item, isHighlighted) => (
						<div
						style={isHighlighted ? styles.highlightedItem : styles.item}
						key={item}
						>{item}</div>
						)}
					/>
					<button 
					className="addurl-save"
					value="save"
					onClick={this.handleURLButtonSX}
					>Save</button>
					<button 
					className="addurl-delete"
					value="delete"
					onClick={this.handleURLButtonSX}
					>X</button>
				</div>
			);
		}

		return (
			<div className="addurlsection">
				{inputfield}
				<button className="addurl-btn" type="button" onClick={this.onClickAddURL}>Add New URL</button>
				{this.checkError()}
			</div>
		);
	}

}

ReactDOM.render(
  <Table />,
  document.getElementById('root')
);


