import React, { Component } from 'react';

// var History = {
// 	'google.com': [1, 2, [30,60], ["js", "styles"], ["day", "week"] ]
// };

//url, urldata, editalarmhandler
class URLAlarmList extends Component {
	render() {
		// var url = this.props.url;
		var urldata = this.props.urldata;

		var dataALARMDUR = urldata[2]; //array of durations
		var dataALARMTYPE = urldata[3]; //array of alarm types
		var dataALARMPER = urldata[4]; //array of per values

		// var alarmNum = dataALARMTYPE.length;
		var allAlarmsDict = [];
		for (var i = 0; i < dataALARMTYPE.length; i++ ) {

			var iAlarm = dataALARMTYPE[i];
			var iDur = dataALARMDUR[i];
			var iPer = dataALARMPER[i];
			
			var iBlob = [iAlarm, iDur, iPer];
			allAlarmsDict.push(iBlob);
		}
		// console.log(allAlarmsDict);
		//sample allAlarmsDict: {js: [30, day], html: [40, week]}
		
		var alarmlist = allAlarmsDict.map(function(blob, i) {
			var alarmtype = blob[0]; //singular value
			var alarmdur = blob[1];
			var alarmper = blob[2];

			if (alarmdur !== 0) {
				return (
					<URLAlarm url={this.props.url} type={alarmtype} dur={alarmdur} per={alarmper} 
					key={i} id={i}
					editalarmhandler={this.props.editalarmhandler}/>
				);
			}

			// return (
			// 		<URLAlarm type={alarmtype} dur={alarmdur} per={alarmper} 
			// 		key={i} />
			// 	);
			return null;
		}, this);
		
		return (
			<div className="urlalarmlist">
			{alarmlist}
			</div>
		);
	}
}

// url, type, dur, per, editalarmhandler
class URLAlarm extends Component {
	constructor(props) {
		super(props);
		this.state = {
			url: this.props.url,
			type: this.props.type,
			durmin: this.props.dur,
			durunit: 'min',
			per: this.props.per,
			id: this.props.id
		}
		this.handleAlarmChange = this.handleAlarmChange.bind(this);
		this.handleAlarmButton = this.handleAlarmButton.bind(this);
	}

	handleAlarmChange(event) {
		event.preventDefault();
		var name = event.target.name;
		var val = event.target.value;

		this.setState({
			[name]: val
		});
	}

	handleAlarmButton(event) {
		event.preventDefault();
		var operation = event.target.name;

		if (operation === "savealarm") {
			var infobundle = {url: this.state.url, type: this.state.type, 
			durmin: this.state.durmin, durunit: this.state.durunit, 
			per: this.state.per, id: this.state.id, operation: "save"};
			this.props.editalarmhandler(infobundle);

		}
		else if (operation === "deletealarm") {
			var infobundle2 = {url: this.state.url, type: this.state.type, 
			durmin: this.state.durmin, durunit: this.state.durunit, 
			per: this.state.per, id: this.state.id, operation: "delete"};			
			this.props.editalarmhandler(infobundle2);
		}
		else return;
	}

	render() {
		

		return (
			<div className="alarm">
				<label>Alarm Type 
				<select className="alarmtype-select" name="type" value={this.state.type} onChange={this.handleAlarmChange}>
					<option value="js">JS</option>
					<option value="images">Images</option>
					<option value="styles">Styles</option>
				</select>
				</label>

				<label> Alarm Duration:
				<input 
					className="alarmdur-input"
					type="text" 
					name="durmin"
					value={this.state.durmin} onChange={this.handleAlarmChange}/>
				<select
					className="alarmdurunit-select"
					name="durunit" value={this.state.durunit} onChange={this.handleAlarmChange} >
					<option value="min">minute(s)</option>
					<option value="hour">hour(s)</option>
				</select>	
				</label>

				<label>per 
				<select
					className="alarmper-select"
					name="per" value={this.state.per} onChange={this.handleAlarmChange} >
					<option value="day">day</option>
					<option value="week">week</option>
				</select>	
				</label>
				
				<button className="alarm-save" name="savealarm" onClick={this.handleAlarmButton}
				>Save</button>
				<button className="alarm-delete" name="deletealarm" onClick={this.handleAlarmButton}
				>X</button>

			</div>
		);
	}
}

module.exports = URLAlarmList;


