// data, newalarmhandler
class URLBlockList extends Component {
	render() {
		// console.log((this.props.data));
		var urlblocks = Object.keys(this.props.data).map(function(key, i) {
			return (
				<URLBlock url={key} urldata={this.props.data[key]}
				newalarmhandler={this.props.newalarmhandler}
				editalarmhandler={this.props.editalarmhandler}
				key={i}/>
			);

		}, this);

		return (
			<div className="urlblocklist">
				{urlblocks}
			</div>
		);
	}
}

// url, urldata, newalarmhandler
class URLBlock extends Component {
	render() {
		return (
			<div className="urlblock">
				<URLHeader url={this.props.url} 
				newalarmhandler={this.props.newalarmhandler}/>
				<URLAlarmList url={this.props.url} urldata={this.props.urldata}
				editalarmhandler={this.props.editalarmhandler}/>
			</div>
		);
	}
}

// url, newalarmhandler
class URLHeader extends Component {
	constructor(props) {
		super(props);

		// this.parseURL = this.parseURL.bind(this);
	}

	// parseURL(url) {
	// 	var cleankey = key.substring(6, key.length - 2)
	// }

	render() {
		return (
			<div className="urlheader">
				{this.props.url}
				<AddAlarmButton url={this.props.url}
				newalarmhandler={this.props.newalarmhandler}/>
			</div>
		);
	}
}

// url, newalarmhandler
class AddAlarmButton extends Component {
	render() {
		return (
			<button className="addalarm" onClick={this.props.newalarmhandler} value={this.props.url}>
			Add Alarm</button>
		);
	}
}



module.exports = URLBlockList;


